@import "../../Styles/Variables/Variables.scss";

.rankSection {
  @include homeContainer;
  margin-top: 1.5rem;
  gap: 1rem;
  z-index: 2;
  .sectionName {
    color: $secondaryFontColor;
    margin-top: 1rem;
  }
  .categories {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    width: 90%;
    overflow-y: scroll;
    height: 70%;
    -ms-overflow-style: none; /* IE and Edge */
    scrollbar-width: none; /* Firefox */
    margin-top: 1rem;

    @media (min-width: 768px) {
      width: 50%;
    }

    @media (min-width: 1024px) {
      width: 30%;
    }

    &::-webkit-scrollbar {
      display: none;
    }

    .rankItem {
      display: flex;
      justify-content: flex-start;
      width: 100%;
      margin-top: 0.5rem;

      .categoryName {
        @include categoryName;
        color: $secondaryFontColor;
      }

      .index {
        width: 100px;
        font-weight: bold;
      }

      .nickName {
        margin-left: 35px;
      }
      .points {
        margin-left: auto;
        min-width: 100px;
      }
    }
  }
}
